import React, { Component } from 'react';
import { List, Card , Button , Image, Typography, Collapse} from 'antd';
import axios from 'axios';
import Cookies from 'js-cookie';
import { connect } from 'react-redux';
import { addCart,checkAuthenticated } from '../actions/auth';
import {fetchCart } from '../actions/cart';


  
    const data = [
    
    ];
    const { Paragraph } = Typography;

    
  
  
  
  

 
class ShopProduct extends Component {
  

  

  state = {
    products: []

  }

  componentDidMount(){
   
    axios.get("http://127.0.0.1:8000/api/").then (res => {
      this.setState({
        products : res.data
      });
    

    })
    

  }
  handleClick= slug => {
    const token = localStorage.getItem('access');

    

    const config = {
      headers: {
        'Authorization': 'JWT ' + token
      }
    };
   
   
    axios.post("http://127.0.0.1:8000/api/addCart/", {slug}, config).then (res => {
      this.props.fetchCart();
      console.log(res.data);
      
    

    })
      
  }
  
  render() {

    

    return (
      <section id="Shop">

      



      
      <div> 
        <List
    grid={{ gutter: 16, column: 4 }}
    dataSource={this.state.products}
    renderItem={item => (
      <List.Item>
        <Card title={item.title}>
              
                    <Image 
                    width={200} align = "start"
                  src={item.Product_Image}/> 
                  
     <br/> <br/>

     <Paragraph id="priceStyle" ellipsis={{ rows: 2, expandable: true, symbol: 'more' }}>
       Product Description:
     {item.Description} 
     </Paragraph><br/>
      <div id="priceStyle">Price ($):{item.price}</div> 
     
     <Button type="primary" block onClick={() => this.handleClick(item.slug)
      
    }> 
     Buy
    </Button>
         </Card>
         
      </List.Item>
    )}
      />
      </div>
   </section>
    );
  }
}

const mapStateToProps = state => ({
  isAuthenticated: state.auth.isAuthenticated
});
const mapDispatchToProps = dispatch => {
  return {
    fetchCart: () => dispatch(fetchCart())
  };
};

export default connect(mapStateToProps,mapDispatchToProps)(ShopProduct);
