import React, { Component } from 'react';
import ReactGA from 'react-ga';
import $ from 'jquery';
import 'antd/dist/antd.css';
import './App.css';
import "semantic-ui-css/semantic.min.css";
import Nav_bar from './Components/Header';
import Footer from './Components/Footer';
import About from './Components/About';
import Login from './Components/Login';
import Contact from './Components/Contact';
import Portfolio from './Components/Portfolio';
import ProductGridView from './containers/ProductGridView';
import ShopProduct from './Components/Shop';
import Activate from './Components/Activate';
import ResetPassword from './Components/ResetPassword';
import ResetPasswordConfirm from './Components/ResetPasswordConfirm';
import ordersummary from './Components/order-summary';
import {BrowserRouter as Router, Switch, Route} from  'react-router-dom';




const Home = ()=>(
  <div>
  <About/>
  <ShopProduct/>
  <Portfolio/>
  </div>

);

class App extends Component {

  constructor(prop){
    super(prop);
    this.state = {
      foo: 'bar',
      resumeData: {}
    };

    ReactGA.initialize('UA-110570651-1');
    ReactGA.pageview(window.location.pathname);

  }

  getResumeData(){
    $.ajax({
      url:'./resumeData.json',
      dataType:'json',
      cache: false,
      success: function(data){
        this.setState({resumeData: data});
      }.bind(this),
      error: function(xhr, status, err){
        console.log(err);
        alert(err);
      }
    });
  }

  componentDidMount(){
    this.getResumeData();

  }
  


  render() {
    return (
          
        <div>
          
         

            
          <Router>
            
         
            <Nav_bar data={this.state.resumeData.main } />
            <Switch>
            <Route exact path="/" component={Home}/>
            <Route exact path="/signup" component={Contact}/>
            <Route exact path="/Login" component={Login}/>
            <Route exact path='/reset-password' component={ResetPassword} />
            <Route exact path='/password/reset/confirm/:uid/:token' component={ResetPasswordConfirm} />
            <Route exact path='/activate/:uid/:token' component={Activate} />
            <Route exact path="/checkout" component={ordersummary} />
            </Switch>
            <Footer data={this.state.resumeData.main}/>
          </Router>

        </div>

    );
  }
}



export default App;
