import React from "react";
import axios from 'axios';
import { Button,Header,Container,Table } from 'semantic-ui-react'
import { connect } from "react-redux";
import { Link, Redirect } from "react-router-dom";
import KhaltiCheckout from "khalti-checkout-web";
import myKey from './khaltiKey';


class ordersummary extends React.Component {

   

  state = {
    data: null,
    error: null,
    loading: false
  };

  componentDidMount() {
    this.handleFetchOrder();
  }

  handleFetchOrder = () => {
    this.setState({ loading: true });
    const token = localStorage.getItem('access');
    const config = {
      headers: {
        'Authorization': 'JWT ' + token
      }
    };
       axios.get(`http://127.0.0.1:8000/api/fetchCart/` ,config)
      .then(res => {
        this.setState({ data: res.data, loading: false });
      })
      .catch(err => {
       
          this.setState({
            error: "You currently do not have an order",
            loading: false
          });
        
        
      });
  };


  
  render() {
    const { data, error, loading } = this.state;
    console.log(data && data.map((order_item) =>  order_item.quantity , 0 ).toString());
    
    let config = {
      
      // replace this key with yours
      "publicKey": "test_public_key_f92b8ed21efe48e1942a23d53d8de4ee",
      "productIdentity":data && data.map((order_item,i) =>  i , 0 ).toString(),
      "productName":data && data.map((order_item,i) =>  order_item.item , 0 ).toString(),
      "productUrl": "http://localhost:3000/",
      "eventHandler": {
          onSuccess (payload) {
            const axios = require('axios');
            const token = localStorage.getItem('access');
            const config = {
              headers: {
                'Authorization': 'JWT ' + token
              }
            };
              axios.delete(`http://127.0.0.1:8000/api/EmptyCart/` ,config)
              .then(res => {
                this.handleFetchOrder();
              })
              .catch(err => {
               
                console.log(error);
                
                
              });
              // hit merchant api for initiating verfication
              console.log(payload);
              

                let data = {
                    "token": payload.token,
                    "amount": payload.amount
                };

               

                axios.get(`https://meslaforum.herokuapp.com/khalti/${payload.token}/${data.amount}/${"test_secret_key_0904958fa53a470cb75213b143dfbed3"}`)
                    .then(response => {
                        console.log(response.data);
                    })
                    .catch(error => {
                        console.log(error);
                    });
                    location.href = "/"

          },
          // onError handler is optional
          onError (error) {
              // handle errors
              console.log(error);
          },
          onClose () {
              console.log('widget is closing');
          }
      },
      "paymentPreference": ["KHALTI", "EBANKING","MOBILE_BANKING", "CONNECT_IPS", "SCT"],
  };

  let checkout = new KhaltiCheckout(config);

    return (
    <Container> 
      <br/>
      <Header>Order Summary</Header>

        {data &&
        <Table celled inverted selectable>
          <Table.Header>
            <Table.Row>
              <Table.HeaderCell>Order number</Table.HeaderCell>
              <Table.HeaderCell>Name</Table.HeaderCell>
              <Table.HeaderCell>quantity</Table.HeaderCell>
              <Table.HeaderCell>price</Table.HeaderCell>
              <Table.HeaderCell>Gross Total</Table.HeaderCell>
            </Table.Row>
          </Table.Header>

          <Table.Body>
            {data.map((order_item, i) =>{
              return(
                <Table.Row key= {order_item.id}>
                  <Table.Cell>{i}</Table.Cell>
                  <Table.Cell>{order_item.item}</Table.Cell>
                  <Table.Cell >{order_item.quantity}</Table.Cell>
                  <Table.Cell >{order_item.item_obj.price}</Table.Cell>
                  <Table.Cell >{order_item.total}</Table.Cell>
              </Table.Row>
              )
            })}
            <Table.Row>
                <Table.Cell></Table.Cell>
                <Table.Cell></Table.Cell>
                <Table.Cell>Total</Table.Cell>
                <Table.Cell>{data.reduce((total,v) =>  total = total + v.total , 0 )}</Table.Cell>
                <Table.Cell textAlign='right'>
                  <Button color = 'purple' onClick={() => checkout.show({amount: data.reduce((total,v) =>  total = total + v.total , 0 )*100}) }>Pay with Khalti</Button>
                </Table.Cell>
              </Table.Row>
          </Table.Body>
        </Table>}
        <br/>
      </Container>
      
      
    )
  }
}



export default ordersummary;
